import React from "react";

// reactstrap components
import {
  Button,
  Container,
} from "reactstrap";

const subtitleTexts = [
  'computer scientist',
  'white hat',
  'software engineer',
  'solution architect',
  'product leader',
];

class LandingPage extends React.Component {
  constructor() {
    super();
    this.state = { textIdx: 0 };
  }

  componentDidMount() {
    this.timeout = setInterval(() => {
      let currentIdx = this.state.textIdx;
      this.setState({ textIdx: (currentIdx + 1)  % subtitleTexts.length });
    }, 1500);
  }

  componentDidUnmount() {
    clearInterval(this.timeout);
  }

  render() {
    let subtitle = subtitleTexts[this.state.textIdx];

    return (
      <div className="page-header header-filter">
        <div className="squares square1" />
        <div className="squares square2" />
        <div className="squares square3" />
        <div className="squares square4" />
        <div className="squares square5" />
        <div className="squares square6" />
        <div className="squares square7" />
        <Container>
          <div className="content-center brand text-left">
            <h1 className="h1-seo">Muhammad <strong className="hidden">Ilham Dedi</strong></h1>
            <h3 className="d-sm-block">
              A passionate <strong>{ subtitle }</strong>
            </h3>
            <div className="btn-wrapper">
              <div className="button-container">
                <Button
                  className="btn-icon btn-simple btn-round btn-neutral"
                  color="default"
                  href="https://www.github.com/milhamdedi"
                >
                  <i className="fab fa-github" />
                </Button>
                <Button
                  className="btn-icon btn-simple btn-round btn-neutral"
                  color="default"
                  href="https://www.linkedin.com/in/milhamdedi"
                >
                  <i className="fab fa-linkedin" />
                </Button>
                <Button
                  className="btn-icon btn-simple btn-round btn-neutral"
                  color="default"
                  href="https://www.twitter.com/_milhamdedi"
                >
                  <i className="fab fa-twitter" />
                </Button>
                <Button
                  className="btn-icon btn-simple btn-round btn-neutral"
                  color="default"
                  href="https://www.instagram.com/milhamdedi"
                >
                  <i className="fab fa-instagram" />
                </Button>
              </div>
            </div>
          </div>
        </Container>
      </div>
    );
  }
}

export default LandingPage;
